.App {
  text-align: left;
  padding-left: 20px;
  font-weight: 300;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.75em;
}

.App-experience {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.clear {
  overflow: hidden;
  zoom: 1;
}

.App-background-list {
  display: block;
  /*list-style-type: disc;*/
  -webkit-margin-before: 1em;
  -webkit-margin-after: 1em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
  -webkit-padding-start: 40px;
  margin-left: -40px;
  list-style-type: none;
  padding-bottom: 10px;
}

ul.App-background-list li {
    float: left;
    margin: 0 12px 12px 0;
}

ul.App-background-list li a {
    display: block;
    text-indent: -9999px;
}

li {
    display: list-item;
    text-align: -webkit-match-parent;
}

ul.App-background-list li a#coinbase {
  background-image: url('/images/coinbase.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 244px;
  height: 50px;
}

ul.App-background-list li a#facebook {
  background-image: url('/images/facebook.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 259px;
  height: 50px;
}

ul.App-background-list li a {
  -webkit-filter: grayscale(60%);
}

ul.App-background-list li a:hover {
  filter: none;
  -webkit-filter: grayscale(0%);
}

ul.App-background-list li a:-webkit-any-link {
  color: -webkit-link;
  cursor: pointer;
  text-decoration: underline;
}

.App-email {
  color: inherit;
  text-decoration: none;
}

.App-border {
  border:1px solid black;
  margin-left:20px;
  margin-top:20px;
  margin-right:20px;
  margin-bottom:20px;
  padding:2ex;
  width: 550px;
}

@media screen and (max-device-width: 550px) {
  .App-border {
    width: 300px;
  }
}



